window.tab_info = false;
window.tab_info2 = false;
window.tab_info3 = false;


const downloadApp = (url) => {
  window.download_url = url;
  closeStep1Tab();

  chrome.tabs.create({url});
}

const appConnect = () => {
  console.log('~appConnect~')

  chrome.storage.local.get(['isBannedIPCompilance', 'isBannedIP'], function (ret) {
    if (ret.isBannedIPCompilance) {
      Storage.app.setConnected(false)
      Proxy.unset();

      //TODO: show it
      // scope.compilanceBlock = "block";

      return;
    }

    if (ret.isBannedIP) {
      Storage.app.setConnected(false)
      Proxy.unset();

      chrome.tabs.create({url: SyncURL.URL_BANNED});
      return;
    }

    Proxy.set();

    const lastChangedTime = Storage.user.getLastChangedTime();
    console.log('lastChangedTime: ', lastChangedTime);

    Messages.toPopup().updateLastTimeChanged(lastChangedTime);

    const loc = Storage.user.getLastLocation();
    console.log('Storage.user.getLastLocation(): ', loc)

    if (loc) {
      console.log('Storage.user.getLocation(): ', Storage.user.getLocation())

      Storage.user.setLocation(loc);

      chrome.storage.local.set({'currentCountryISO': loc.countryiso});
      chrome.storage.local.set({'currentCity': loc.city});
    }

    Messages.toPopup().updateLastLocation(loc);

    Storage.app.setConnected(true);

    changeBadgeOnConnection();
  });
}

const locationChange = (_nearby, providedCountryISO, providedCity) => {

  chrome.storage.local.get('main_residential_checked', ({main_residential_checked}) => {
    console.log('main_residential_checked: ', main_residential_checked);
    console.log('providedCountryISO: ', providedCountryISO);
    console.log('providedCity: ', providedCity);

    let is_residential = false;
    if (typeof main_residential_checked !== 'undefined' && main_residential_checked) {
      is_residential = true;
    }

    chrome.storage.local.get('app_non_residential', function ({app_non_residential}) {
      console.log('2 app_non_residential: ', app_non_residential);

      if (app_non_residential) {
        App.changeIPCountryCityNew(providedCountryISO, providedCity, false, is_residential);
      } else {
        App.changeIPCountryCity(providedCountryISO, providedCity, false);
      }
    });
  });
};

