const appDisconnect = () => {
  console.log('~appDisconnect~')

  Proxy.unset();

  Storage.app.setConnected(false);

  console.log('Storage.user.getDefaultLocation(): ', Storage.user.getDefaultLocation())

  const lastLocation = Storage.user.getLocation();


  const oldLocation = Storage.user.getDefaultLocation();

  Storage.user.setLocation(oldLocation);
  Storage.user.setLastLocation(lastLocation);

  Messages.toPopup().updateLastLocation(oldLocation);

  Badge.setDisconnected();
}


const initMessageListeners = () => {
  chrome.runtime.onMessageExternal.addListener(function (msg, sender, sendResponse) {
    if (msg.action === "check_installed") {
      sendResponse({is_installed: "yes"});
    }
  });

  chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
    console.log('BG MESSAGE: ' + JSON.stringify(message));

    switch (message.action) {
      case MSG_DOWNLOAD_APP:
        downloadApp(message.url);

        sendResponse();
        break;

      case MSG_LOCATION_SELECTED:
        Badge.setConnected(message.countryISO);

        if (Storage.app.isInstalled()) {
          locationChange(false, message.countryISO, message.city);
          sendResponse();
        } else {
          (async () => {
            const user = await Storage.user.getUser();
            console.log('user: ', user);

            const info = await ApiBasedApp.getServerInfo(user, message.countryISO, message.city);
            console.log('info: ', info);

            ApiBasedApp.setProxy(info);

            sendResponse();
          })();
        }

        break;

      case MSG_SET_NEXT_NEARBY:
        if (Storage.app.isInstalled()) {
          locationChange(true, message.countryISO, message.city || 'Any');
          sendResponse();
        } else {
          //TODO: duplicated from above
          (async () => {
            const user = await Storage.user.getUser();
            console.log('user: ', user);

            const info = await ApiBasedApp.getServerInfo(user, message.countryISO, message.city);
            console.log('info: ', info);

            ApiBasedApp.setProxy(info);

            sendResponse();
          })();
        }
        break;

      case MSG_APP_GET_ACCOUNT_INFO:
        //TODO: move to App
        const msg = {name: message.action, args: JSON.stringify(message.args)};
        App.sendMessage(msg, sendResponse);
        break;

      //
      // Background-related events
      //
      /*************************************************/
      case MSG_GET_FLAG_ICON:
        console.log('Storage.app.isInstalled(): ', Storage.app.isInstalled());

        if (Storage.app.isInstalled()) {
          App.getCountryFlagName(message.iso, iconName => {
            const path = message.medium ? 'img/flags-medium/' + iconName.toLowerCase() : 'img/flags/' + iconName;
            sendResponse(chrome.runtime.getURL(path));
          });
        } else {
          const path = (message.medium ? 'img/flags-medium/' + message.iso.toLowerCase() : 'img/flags/' + message.iso) + '.png';
          sendResponse(chrome.runtime.getURL(path));
        }
        break;

      case MSG_IS_PROXY_SET:
        sendResponse(Proxy.isSet());
        break;

      //
      // API-related events
      //
      /*************************************************/
      case MSG_STARTUP_DATA:
        BackendApi.startupData(message.username, message.passHash)
          .then(sendResponse);
        break;

      case MSG_API_LOG_IN:
        BackendApi.logIn(message.username, message.passHash)
          .then(sendResponse);
        break;

      case MSG_API_SUBMIT_RATING:
        BackendApi.submitRating(message.rating, Storage.user.getEmail() || '')
          .then(sendResponse);
        break;

      case MSG_API_SUBMIT_FEEDBACK:
        BackendApi.submitFeedback(message.msg, Storage.user.getEmail() || '')
          .then(sendResponse);
        break;

      case MSG_TOGGLE_CONNECTION:
        console.log('Storage.app.isInstalled(): ', Storage.app.isInstalled());

        if (Storage.app.isInstalled()) {
          if (message.connected) {
            appConnect();
          } else {
            appDisconnect();
          }

          sendResponse();
        } else {
          if (message.connected) {
            ApiBasedApp.startKeepAlive().then(serverInfo => {
              console.log('Keep_alive sent! serverInfo: ', serverInfo);

              if (serverInfo) {
                Badge.setConnected(serverInfo.country_code);
                ApiBasedApp.setProxy(serverInfo);
                sendResponse();
              } else {
                // Premium Expired
                sendResponse(STATUS_PREMIUM_EXPIRED);
              }

              // Proxy.
              // data:
              //   city_name: "Amsterdam"
              // country_code: "NL"
              // country_name: "Netherlands"
              // hostname: "142.93.139.162"
              // password: "206c80413b9a96c1312cc346b7d2517b84463edd"
              // port: 501
              // region_name: "Amsterdam"
              // username: "viktar@test.com"

            });
          } else {
            ApiBasedApp.disconnect();

            sendResponse();
          }
        }

        break;

      case MSG_GET_USER:
        Storage.user.getUser()
          .then(sendResponse);
        break;

      case MSG_LOG_IN:
        Storage.user.setUser({email: message.email, password: message.password});
        App.logIn(message.email, message.password);

        sendResponse();
        break;
    }

    return true;
  });
};

const resetLocation = () => {
  const oldLocation = Storage.user.getDefaultLocation();
  console.log('@@oldLocation: ', oldLocation)

  if (oldLocation) {
    Storage.user.setLocation(oldLocation);
  }
}

const onExtensionInstalled = () => {
  chrome.runtime.onInstalled.addListener(function (details) {

    if (details.reason === "install") {
      chrome.storage.local.get('was_shown_info', function (res) {

        AsyncURL.step1().then(url => {
          chrome.tabs.create({url}, function (tab) {
            window.tab_info = tab.id;
            window.download_id = false;

            chrome.downloads.onChanged.addListener(function (downloadItem) {
              console.log('chrome.downloads.onChanged: ', downloadItem);
              console.log('window.download_id: ', window.download_id);

              if (window.download_id == downloadItem.id) {
                console.log('window.tab_info2L: ', window.tab_info2);
                console.log('downloadItem: ', downloadItem);

                if (window.tab_info2 == false && typeof downloadItem.state !== 'undefined' && downloadItem.state.current == "complete") {
                  console.log('opending url2...');
                  AsyncURL.step2().then(urlStep2 => {
                    console.log('urlStep2: ', urlStep2);
                    chrome.tabs.create({url: urlStep2}, function (tab) {
                      window.tab_info2 = tab.id;
                    });
                  });
                }
              }
            });

            chrome.downloads.onCreated.addListener(function (downloadItem) {
              console.log('downloadItem: ', downloadItem);
              console.log('window.download_url: ', window.download_url);

              if (downloadItem.url == window.download_url) {
                window.download_id = downloadItem.id;
              }

              if (window.download_id == false) {
                if (downloadItem.url.toLowerCase().includes("tuxler") && (
                  downloadItem.url.toLowerCase().includes(".exe") ||
                  downloadItem.url.toLowerCase().includes(".pkg")
                )) {
                  window.download_id = downloadItem.id;
                }
              }
            });
          });
        });
      });
    }
  });
};

const initExtension = () => {
  initMessageListeners();
  resetLocation();
  App.start();
  ApiBasedApp.startUserCheck();
  onExtensionInstalled();
};

initExtension();