import {Storage} from "./storage";
import {BackendApi, STATUS_OK, STATUS_PREMIUM_EXPIRED} from "./api";
import {Notifications} from "./notifications";
import {Proxy} from "./proxy";
import Messages from "./messages";
import {Badge} from "./badge";


const KEEP_ALIVE_INTERVAL = 30 * 1000;
const USER_CHECK_INTERVAL = 30 * 60 * 1000;

export const ApiBasedApp = {
  async startKeepAlive() {
    console.log('~startKeepAlive~');

    const user = await Storage.user.getUser();
    console.log('user: ', user);
    if (!user) {
      return;
    }

    await BackendApi.keepAlive(user.email, user.password);

    this.keepAliveIntervalId = setInterval(() => {
      BackendApi.keepAlive(user.email, user.password);
    }, KEEP_ALIVE_INTERVAL);


    const selectedLocation = await Storage.local.get(['selectedCountryISO', 'selectedCity']);
    console.log('selectedLocation: ', selectedLocation);

    return await this.getServerInfo(user, selectedLocation.selectedCountryISO, selectedLocation.selectedCity);
  },

  startUserCheck() {
    this._doUserCheck();

    this.userCheckIntervalId = setInterval(() => {
      this._doUserCheck();
    }, USER_CHECK_INTERVAL);
  },

  _stopUserCheck() {
    if (this.userCheckIntervalId) {
      clearInterval(this.userCheckIntervalId);
      this.userCheckIntervalId = null;
    }
  },

  async _doUserCheck() {
    const user = await Storage.user.getUser();
    if (!user) {
      return;
    }

    const userStatus = await BackendApi.logIn(user.email, user.password);

    if (userStatus.status === STATUS_PREMIUM_EXPIRED) {
      Notifications.showMessage('Your premium account is expired');
      Storage.user.setPremium(false);

      this._stopUserCheck();

      //TODO: and if connected
      if (!Storage.app.isInstalled()) {
        this.disconnect();
      }
    }

    if (userStatus.status === STATUS_OK) {
      Storage.user.setPremium(true);
    }
  },

  async getServerInfo(user, countryISO, city) {
    const serverInfo = await BackendApi.getNewServer(user.email, user.password, countryISO, city);
    console.log('serverInfo: ', serverInfo);

    if (serverInfo.status === STATUS_OK) {
      return serverInfo.data;
    }

    if (serverInfo.status === STATUS_PREMIUM_EXPIRED) {
      Storage.user.setPremium(false);
    }

    return null;
  },

  setProxy(serverInfo) {
    //TODO: add error handling?
    if (!serverInfo) {
      return false;
    }

    Proxy.setSocksProxy({proxy: serverInfo.hostname, port: serverInfo.port});

    const now = Date.now();
    Storage.user.setLastChangedTime(now);
    Messages.toPopup().updateLastTimeChanged(now);

    const loc = {
      city: serverInfo.city_name,
      country: serverInfo.country_name,
      region: serverInfo.region_name,
      countryiso: serverInfo.country_code
    };

    console.log('location: ', loc);

    //TODO: add error handling?
    //TODO: change location in callback?

    Storage.user.setLocation(loc);
    Storage.user.setLastLocation(loc);


    chrome.storage.local.set({'currentCountryISO': serverInfo.country_code});
    chrome.storage.local.set({'currentCity': serverInfo.city_name});

    Messages.toPopup().updateLastLocation(loc);
    return true;
  },
  disconnect() {
    this.stopKeepAlive();
    Badge.setDisconnected();
    Proxy.unset();

    const loc = Storage.user.getDefaultLocation();
    console.log('@loc: ', loc);
    Storage.user.setLocation(loc);

    Messages.toPopup().updateLastLocation(loc);
  },
  stopKeepAlive() {
    if (this.keepAliveIntervalId) {
      clearInterval(this.keepAliveIntervalId);
      this.keepAliveIntervalId = null;
    }
  }
};
